import { useEffect, useRef, useState } from "react";
import axios from "axios";
import OrderModal from "../../components/OrderModal";
import Pagination from "../../components/Pagination";
import { Modal } from "bootstrap"; // Modal


export default function AdminOrders() {
  const [orders, setOrders] = useState([]);
  const [pagination, setPagination] = useState({});
  const [tempOrder, setTempOrder] = useState({});//

  const orderModal = useRef(null);// ref() null({current:null})

  useEffect(() => {
    orderModal.current = new Modal('#orderModal', {// id Modal  useRef (productModal)
      backdrop: 'static',//
      keyboard: false    //esc
    });
    getOrders();
  }, []);

  const getOrders = async (page = 1) => { //page1
    const orderRes = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/orders?page=${page}`);//
    setOrders(orderRes.data.orders);
    setPagination(orderRes.data.pagination);
  }

  const openOrderModal = (order) => {
    setTempOrder(order);
    orderModal.current.show();
  }
  const closeOrderModal = () => {
    orderModal.current.hide();
  }

  const deleteOrder = async (id) => {
    try {
      const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/order/${id}`);
      if (res.data.success) {
        alert("");
        getOrders();//
      }
    } catch (error) {
      alert("");
    }
  }

  return (<>
    <div className="p-3">
      <OrderModal
        closeProductModal={closeOrderModal} //closeOrderModal propcloseProductModal
        getOrders={getOrders}
        tempOrder={tempOrder}
      />
      <h3></h3>
      <hr />
      <table className="table">
        <thead>
          <tr>
            <th scope='col'> id</th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
          </tr>
        </thead>
        <tbody>
          {orders?.map((order) => {
            return (
              <tr key={order.id}>
                <td>{order.id}</td>
                <td>
                  {order.user?.name}
                  {order.user?.email}
                </td>
                <td>${order.total}</td>
                <td>
                  {order.is_paid ? (
                    <span className='text-success fw-bold'></span>
                  ) : (
                    ''
                  )}
                </td>
                <td>
                  {order.paid_date
                    ? new Date(order.paid_date * 1000).toLocaleString()
                    : ''}
                </td>
                <td>{order.message}</td>

                <td>
                  <button
                    type='button'
                    className='btn btn-primary btn-sm'
                    onClick={() => {
                      openOrderModal(order);
                    }}
                  >
                    
                  </button>
                </td>
                <td>
                  <button
                    type='button'
                    className='btn btn-outline-danger btn-sm'
                    onClick={() => {
                      if (window.confirm("")) {
                        deleteOrder(order.id);
                      }
                    }}
                  >
                    
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <Pagination pagination={pagination} changePage={getOrders} />
    </div>
  </>)
}