import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { MessageContext, handleSuccessMessage, handleErrorMessage } from "../context/messageContext";


export default function ProductModal({ closeProductModal, getProducts, type, tempProduct }) {
  const [tempData, setTempData] = useState({
    title: "",
    category: "",
    origin_price: 100,
    price: 300,
    unit: "",
    description: "",
    content: "",
    is_enabled: 1,
    imageUrl: "",
    imagesUrl: [],
  });

  const [, dispatch] = useContext(MessageContext) //message

  useEffect(() => {//typecreateTempDataedit tempProduct 
    if (type === 'create') {
      setTempData({
        title: "",
        category: "",
        origin_price: 100,
        price: 300,
        unit: "",
        description: "",
        content: "",
        is_enabled: 1,
        imageUrl: "",
        imagesUrl: [],
      });
    } else if (type === 'edit') {
      setTempData(tempProduct);
    }
  }, [type, tempProduct]);// type  tempProduct 

  const handleChange = (e) => { //onChangehandleChange(e) e.target  name  valuesetTempData

    const { value, name } = e.target; //e.targetname()value()
    if (['price', 'origin_price'].includes(name)) { //name
      setTempData({
        ...tempData,
        [name]: Number(value)//
      });
    } else if (name === 'is_enabled') {
      setTempData({
        ...tempData,
        [name]: +e.target.checked,//e.target.checked+true1false0
      })
    } else {
      setTempData({
        ...tempData,
        [name]: value
      })
    }
  };

  const submit = async () => {
    try {
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`
      let method = 'post';
      if (type === 'edit') {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`;
        method = 'put';
      }//submitapi
      const res = await axios[method](
        api,
        {
          data: tempData
        });//api data tempDatasubmittempData

      handleSuccessMessage(dispatch, res);
      closeProductModal();//submitmodal
      getProducts();//
    } catch (error) {
      handleErrorMessage(dispatch, error);
    }
  };

  const [tempImage, setTempImage] = useState("");//

  const handleUpload = async (file, type = 'main') => { //filetypehandleupload
    if (!file) { return }
    const formData = new FormData(); //formData
    formData.append('file-to-upload', file); //file-to-uploadapiappendformData

    try {
      const res = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`, formData);//formData multipart/form-data 
      if (res.data.success) {
        if (type === 'main') {
          setTempData((pre) => ({
            ...pre, //
            imageUrl: res.data.imageUrl, //api
          }));
        } else {
          setTempData((pre) => ({
            ...pre, //
            imagesUrl: [...(pre.imagesUrl || []), res.data.imageUrl], // pre.imagesUrl  undefined imagesUrl 
          }));
        }

      }
    } catch (error) {
      console.log(error)
    }

    return (
      <div className="modal fade" id="productModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h2 className="modal-title fs-5" id="exampleModalLabel">
                {type === 'create' ? '' : `${tempData.title}`}
              </h2>
              <button type="button" className="btn-close" aria-label="Close" onClick={closeProductModal}></button>
              {/*data-bs-dismissonClick={closeProductModal} */}
            </div>
            <div className="modal-body">
              <div className='row'>
                <div className='col-sm-4'>
                  <div className='form-group mb-2'>
                    <label className='w-100' htmlFor='image'>
                      
                      <input
                        type='text'
                        name='imageUrl'
                        id='image'
                        placeholder=''
                        className='form-control'
                        value={tempData.imageUrl}
                        onChange={handleChange}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2'>
                    <label className='w-100' htmlFor='customFile'>
                       
                      <input
                        type='file'
                        id='customFile'
                        className='form-control'
                        onChange={(e) => handleUpload(e.target.files[0], 'main')} //inputuploadFile
                      />
                    </label>
                  </div>
                  {tempData.imageUrl && (
                    <img src={tempData.imageUrl} alt="" className="img-fluid" />
                  )}
                  {/*tempData<img>*/}
                  <div className='form-group my-4'>
                    <label className='w-100'>
                      
                      <div className="input-group mb-2">

                        <input
                          type="text"
                          className="form-control"
                          placeholder=""
                          value={tempImage}
                          onChange={(e) => setTempImage(e.target.value)}
                        />
                        <button
                          type="button"
                          className="btn btn-outline-primary"
                          onClick={() => {
                            if (!tempImage) return;
                            setTempData((pre) => ({
                              ...pre,
                              imagesUrl: [...pre.imagesUrl, tempImage]
                            }));
                            setTempImage("");  // 
                          }}
                        >
                          
                        </button>
                      </div>
                    </label>
                    <div className='form-group mb-2'>
                      <label className='w-100' htmlFor='customFileSub'>
                         
                        <input
                          type='file'
                          id='customFileSub'
                          className='form-control'
                          onChange={(e) => handleUpload(e.target.files[0], 'sub')}
                        />
                      </label>
                    </div>
                  </div>
                  {tempData.imagesUrl && tempData.imagesUrl.map((img, i) => (
                    <div key={i} className="mb-2 position-relative">
                      <img src={img} alt={` ${i}`} className="img-fluid" />
                      <button
                        type="button"
                        className="btn btn-sm btn-danger position-absolute"
                        style={{ top: '10px', right: '10px' }}
                        onClick={() => {
                          // 
                          setTempData((pre) => ({
                            ...pre,
                            imagesUrl: pre.imagesUrl.filter((_, index) => index !== i)
                          }));
                        }}
                      >
                        
                      </button>
                    </div>
                  ))}


                </div>
                <div className='col-sm-8'>
                  {/*<pre>
                                    {JSON.stringify(tempData)}
                                </pre>*/}
                  <div className='form-group mb-2'>
                    <label className='w-100' htmlFor='title'>
                      
                      <input
                        type='text'
                        id='title'
                        name='title'
                        placeholder=''
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.title}
                      />
                    </label>
                  </div>
                  <div className='row'>
                    <div className='form-group mb-2 col-md-6'>
                      <label className='w-100' htmlFor='category'>
                        
                        <input
                          type='text'
                          id='category'
                          name='category'
                          placeholder=''
                          className='form-control'
                          onChange={handleChange}
                          value={tempData.category}
                        />
                      </label>
                    </div>
                    <div className='form-group mb-2 col-md-6'>
                      <label className='w-100' htmlFor='unit'>
                        
                        <input
                          type='unit'
                          id='unit'
                          name='unit'
                          placeholder=''
                          className='form-control'
                          onChange={handleChange}
                          value={tempData.unit}
                        />
                      </label>
                    </div>
                  </div>
                  <div className='row'>
                    <div className='form-group mb-2 col-md-6'>
                      <label className='w-100' htmlFor='origin_price'>
                        
                        <input
                          type='number'
                          id='origin_price'
                          name='origin_price'
                          placeholder=''
                          className='form-control'
                          onChange={handleChange}
                          value={tempData.origin_price}
                        />
                      </label>
                    </div>
                    <div className='form-group mb-2 col-md-6'>
                      <label className='w-100' htmlFor='price'>
                        
                        <input
                          type='number'
                          id='price'
                          name='price'
                          placeholder=''
                          className='form-control'
                          onChange={handleChange}
                          value={tempData.price}
                        />
                      </label>
                    </div>
                  </div>
                  <hr />
                  <div className='form-group mb-2'>
                    <label className='w-100' htmlFor='description'>
                      
                      <textarea
                        type='text'
                        id='description'
                        name='description'
                        placeholder=''
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.description}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2'>
                    <label className='w-100' htmlFor='content'>
                      
                      <textarea
                        type='text'
                        id='content'
                        name='content'
                        placeholder=''
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.content}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2'>
                    <div className='form-check'>
                      <label
                        className='w-100 form-check-label'
                        htmlFor='is_enabled'
                      >
                        
                        <input
                          type='checkbox'
                          id='is_enabled'
                          name='is_enabled'
                          placeholder=''
                          className='form-check-input'
                          onChange={handleChange}
                          checked={!!tempData.is_enabled}//true false!!Boolean(tempData.is_enabled)
                        />
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" onClick={closeProductModal} ></button>
              {/*data-bs-dismissonClick={closeProductModal} */}
              <button type="button" className="btn btn-primary" onClick={submit}></button>
            </div>
          </div>
        </div>
      </div>
    )
  }




}