//formonsubmitenter

import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const navigate = useNavigate();

  const [data, setData] = useState({
    username: '',
    password: ''
  });

  const [loginState, setLoginState] = useState({});//

  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value });//usernamepassword[name]
  }

  const submit = async (e) => {
    e.preventDefault(); //
    try {
      const res = await axios.post('/v2/admin/signin', data);// POST   API /v2/admin/signindata
      const { token, expired } = res.data; // token  expired (Token)

      document.cookie = `answerToken=${token}; expires=${new Date(expired)} `; // token  cookie  answerTokenexpires=${new Date(expired)}  cookie 

      if (res.data.success) {
        axios.defaults.headers.common['Authorization'] = token;//token  Axios 
        navigate('/admin/products');
      }
    } catch (error) {
      setLoginState(error.response.data);//d-block
    }

    //token
  }



  return (<div className="container py-5">
    <div className="row justify-content-center">
      <div className="col-md-6">
        <h2></h2>

        <div className={`alert alert-danger ${loginState.message ? 'd-block' : 'd-none'}`} role="alert">
          {/*loginState.messageloerror.response.datad-blockd-noned-none*/}
          {loginState.message}
        </div>

        <form onSubmit={submit}>
          <div className="mb-2">
            <label htmlFor="email" className="form-label w-100">
              Email
              <input id="email" className="form-control" name="username" type="email" placeholder="Email Address" onChange={handleChange} />
            </label>
          </div>
          <div className="mb-2">
            <label htmlFor="password" className="form-label w-100">
              
              <input type="password" className="form-control" name="password" id="password" placeholder="name@example.com" onChange={handleChange} />
            </label>
          </div>
          <button type="submit" className="btn btn-primary"></button>
        </form>

      </div>
    </div>
  </div>)
}

